import React from 'react';
import QueueAnim from 'rc-queue-anim';
import Basic from './Basic';
import MiniVersion from './MiniVersion';
import WithIcon from './WithIcon';
import SwitchStep from './SwitchStep';
import Vertical from './Vertical';
import VerticalMiniVersion from './VerticalMiniVersion';
import ErrorStatus from './ErrorStatus';
import DotStyle from './DotStyle';
import CustomizedDotStyle from './CustomizedDotStyle';

const Page = () => {
  return(
    <div className="container-fluid with-maxwidth no-breadcrumbs">
      <article className="article demo-style-steps">
        <h2 className="article-title">Steps</h2>

        <QueueAnim type="bottom" className="ui-animate">
          <div key="1">
            <Basic />
          </div>
          <div key="2">
            <MiniVersion />
          </div>
          <div key="3">
            <WithIcon />
          </div>
          <div key="4">
            <SwitchStep />
          </div>
          <div key="5">
            <Vertical />
          </div>
          <div key="6">
            <VerticalMiniVersion />
          </div>
          <div key="7">
            <ErrorStatus />
          </div>
          <div key="8">
            <DotStyle />
          </div>
          <div key="9">
            <CustomizedDotStyle />
          </div>
        </QueueAnim>

      </article>
    </div>
  )
}

module.exports = Page;


